<template>
  <div class="row" style="width:100%;">
    <div class="col-lg-12 text-center" style="background: #fff; min-height: 75px; display: table;">
      <p class="verify" v-html="message" v-if="error"></p>
      <p class="verify" v-if="!error">Your account has been verified. You can go back to the <router-link to="/">main page</router-link>.</p>
    </div>
  </div>
</template>
<style>
  .verify {
    font-weight: bold !important;
    vertical-align: middle;
    display: table-cell;
  }
</style>

<script>
  import fireBase from '../../../firebase/vueFire'
  export default {
    name: 'Verify',
    data () {
      return {
        message: 'Verifying Token...',
        error: true
      }
    },
    mounted () {
      var auth = fireBase.auth()
      if (this.$route.query.mode === 'verifyEmail') {
        if (this.$route.query.oobCode) {
          auth.applyActionCode(this.$route.query.oobCode).then((response) => {
            console.log(response)
            this.error = false
          }).catch((error) => {
            console.log(error)
            this.message = 'The verification code is invalid or expire. Please check your email or contact Best Bargains team for help.'
          })
        }
      } else {
        this.message = 'The verification setting is invalid .'
      }
    }
  }
</script>



